import React from "react";
import {Field, reduxForm} from "redux-form";
import {createStandartForm, Input} from "../FormsControls/FormControls";
import {required} from "../../Utilits/validators/validators";
import {loginThuCr} from "../../Redux/Auth-reducer";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";
import cl from "../FormsControls/FormControl.module.css"

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            {createStandartForm("Email",'email',Input,[required])}
            {createStandartForm("Password",'password',Input,[required], {type:"password"})}
            {createStandartForm(null,'rememberMe',Input,[], {type:"checkbox", className:cl.dsadasd}, "Remember me")}

            <div>
                <button>Login</button>
            </div>
            {props.error && <div className={cl.commonErrorDiv}>{props.error}</div>}
        </form>)
}

const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)

const Login = (props) => {

    const onSubmit = (formData) => {
       props.loginThuCr(formData.email, formData.password, formData.rememberMe);
    }

    return <div>{ props.isAuth ? <Redirect to={'/profile'}/>
    :
    <div className={cl.loginFormDiv}>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div> }</div>
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect (mapStateToProps, {loginThuCr}) (Login)