import React from 'react';
import './App.css';
import NavBar from './components/Navbar/Navbar';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings'
import {HashRouter, Route, withRouter} from 'react-router-dom';
import HeaderContainer from "./components/Header/HeaderContainer";
import LoginPage from "./components/Login/Login";
import {compose} from "redux";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./Redux/App-reducer";
import Preloader from "./assets/preloader/Preloader";
import store from "./Redux/redux-store";
import {withSuspense} from "./hoc/WithSuspense";

const DialogsContainer = React.lazy ( () => import(`./components/Dialogs/Dialogs-container`));
const UsersContainer = React.lazy( () => import (`./components/Users/Users-Container`));
const ProfileContainer = React.lazy( () => import (`./components/Profile/ProfileContainer`));

class App extends React.Component {

    componentDidMount() {
        this.props.initializeApp()
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <NavBar State={this.props.store.getState().NavBar}/>
                <div className='content'>
                    <Route path='/profile/:userId?'
                           render={withSuspense(ProfileContainer)}
                    />
                    <Route path='/users'
                           render={withSuspense(UsersContainer)}
                    />
                    <Route path='/dialogs'
                           render={withSuspense(DialogsContainer)}
                    />
                    <Route path='/news' render={() => <News/>}/>
                    <Route path='/music' render={() => <Music/>}/>
                    <Route path='/settings' render={() => <Settings/>}/>
                    <Route path='/login' render={() => <LoginPage/>}/>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
});

const AppContainer = compose(
    withRouter,
    connect(mapStateToProps, {initializeApp})
)(App);

const AppSamuraiJS = () => {
    return (
        <HashRouter>
            <Provider store={store}>
                <AppContainer store={store}/>
            </Provider>
        </HashRouter>
    );
}

export default AppSamuraiJS;

