import React from "react";

export const required = (values) => {
    if (!values) return 'Field is required';
    return undefined;
}

export const maxLengthPostValidCreator = (maxLength) => (value) => {
    if (value.length > maxLength) return `Max length is ${maxLength} symbols`;
    return undefined;
}

export const requiresMessage = (values) => {
    if (!values) return "Message is empty";
    return undefined;
}

export const messageMaxLength = (maxLength) => (values) => {
    if (values.length > maxLength) return `The message should be no longer then ${maxLength} characters`;
    return undefined
}
