import React from 'react';
import cl from './Header.module.css';
import {NavLink} from "react-router-dom";
import loginImg from '../../assets/images/login_Color.png';
import userAuthIcon from '../../assets/images/login_user.png';

const Header = (props) => {
    return <header className={cl.header}>
        <img className={cl.icon}
             src='https://placeit-assets1.s3-accelerate.amazonaws.com/custom-pages/make-a-gaming-logo/gaming-logo-maker-for-an-rpg-guild.png'/>
        <div className={cl.loginBlock}>
            {props.isAuth ?
                <div>
                    {props.login}
                    <img className={cl.loginIcon} src={userAuthIcon}/>
                    <button onClick={props.logoutThuCr}>Logout</button>
                </div>
                : <NavLink to='login'>
                    Login
                    <img className={cl.loginIcon} src={loginImg}/>
                </NavLink>}
        </div>
    </header>
}

export default Header;