const ADD_MESSAGE = 'ADD-MESSAGES';

let initialState = {
    UserData: [
        {id: 1, name: 'Dima', ImgSrc: 'https://www.seaicons.com/wp-content/uploads/2015/06/Horse-icon.png'},
        {id: 2, name: 'Masha', ImgSrc: 'https://vk.com/sticker/1-13643-128'},
        {
            id: 3,
            name: 'Erik',
            ImgSrc: 'https://image.freepik.com/free-vector/couple-cats-fall-love-icon-cat-love-animal-icon-white-isolated_138676-511.jpg'
        },
        {id: 4, name: 'Sasha', ImgSrc: 'https://img.icons8.com/plasticine/2x/car.png'},
        {
            id: 5,
            name: 'Leha',
            ImgSrc: 'https://icons.iconarchive.com/icons/graphicloads/food-drink/256/tea-icon.png'
        }
    ],
    MessageData: [
        {id: 1, message: 'Hi!'},
        {id: 2, message: 'How are You?'},
        {id: 3, message: 'I`m fine!'},
        {id: 4, message: 'И'},
        {id: 5, message: 'К тому же'},
        {id: 6, message: 'Это'},
        {id: 7, message: 'Совершенно бесплатно'},
    ]
}

const dialogReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            let newMessage = {
                id: 8,
                message: action.newMessage,
            };
            let stateCopy = {...state};
            stateCopy.MessageData = [...state.MessageData];
            stateCopy.MessageData.push(newMessage);
            return stateCopy;
        }
        default:
            return state;
    }
}

export const AddMessageActionCreator = (newMessage) => ({type: ADD_MESSAGE, newMessage});

export default dialogReducer;