import {ProfileAPI, usersAPI} from "../api/ApiJs";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
    PostsData: [
        {
            id: 1,
            imgSrc: 'https://image.freepik.com/free-vector/couple-cats-fall-love-icon-cat-love-animal-icon-white-isolated_138676-511.jpg',
            name: 'Erik', message: 'Its my first post', likeCount: 12
        },
        {
            id: 2,
            imgSrc: 'https://vk.com/sticker/1-13643-128',
            name: 'Masha', message: 'Hi, how are you?', likeCount: 144
        }
    ],
    newPostText: 'new text',
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5,
                message: action.newPostText,
                likeCount: 0,
                name: 'Leha',
                imgSrc: 'https://icons.iconarchive.com/icons/graphicloads/food-drink/256/tea-icon.png',
            };
            let stateCopy = {...state};
            stateCopy.PostsData = [...state.PostsData];
            stateCopy.PostsData.push(newPost);
            return stateCopy;
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {...state, status: action.status}
        }
        case DELETE_POST: {
            return {...state, PostsData: state.PostsData.filter(p => p.id != action.postId)}
        }
        default:
            return state;
    }
}

export const setProfile = (userId) => async (dispatch) => {
    let response = await usersAPI.setProfile(userId)

    dispatch(setUserProfile(response.data));
}

export const getStatus = (UserId) => async (dispatch) => {
    let response = await ProfileAPI.getStatus(UserId)

    dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await ProfileAPI.updateStatus(status)

    if (response.data.resultCode === 0) {
        dispatch(setStatus(status));
    }
}

export const deletingPost = (postId) => ({type: DELETE_POST, postId})
export const AddPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})

export default profileReducer;