import React from 'react';
import cl from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import FriendsEl from "./FriendsElements/FriendsElement";

const NavBar = (props) => {

    let friendsElement = props.State.FriendsElementData.map ( el =>
        <FriendsEl src={el.ImgSrc} name={el.name} />
    )

    return <nav className={cl.nav}>
        <div className={`${cl.item} ${cl.active}`}>
            <NavLink to='/profile' activeClassName={cl.active}>Profile</NavLink>
        </div>
        <div className={cl.item}>
            <NavLink to='/dialogs' activeClassName={cl.active}>Dialogs</NavLink>
        </div>
        <div className={cl.item}>
            <NavLink to='/news' activeClassName={cl.active}>News</NavLink>
        </div>
        <div className={cl.item}>
            <NavLink to='/music' activeClassName={cl.active}>Music</NavLink>
        </div>
        <div className={cl.item}>
            <NavLink to='/settings' activeClassName={cl.active}>Settings</NavLink>
        </div>
        <div className={cl.item + ' ' + cl.friendsEl}>
            <NavLink to='/users' activeClassName={cl.active}>Friends</NavLink>
            <div>
                {friendsElement}
            </div>
        </div>
    </nav>
}

export default NavBar;