import {AuthAPI} from "../api/ApiJs";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'auth/SET_USER_DATA';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            };

        default:
            return state;
    }
}

export const authThu = () => async (dispatch) => {
    let response = await AuthAPI.getAuth()

    if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data;
        dispatch(setUserAuthData(id, email, login, true));
    }
}

export const setUserAuthData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA,
    payload: {userId, email, login, isAuth}
});

export const loginThuCr = (email, password, rememberMe) => async (dispatch) => {

    let response = await AuthAPI.login(email, password, rememberMe)

    if (response.data.resultCode === 0) {
        dispatch(authThu())
    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "something go wrong";
        dispatch(stopSubmit('login', {_error: message}));
    }
}

export const logoutThuCr = () => async (dispatch) => {
    let response = await AuthAPI.logout()

    if (response.data.resultCode === 0) {
        dispatch(setUserAuthData(null, null, null, false));
    }
}

export default authReducer;